console.log('Task 2');

let number = 0;
do{
    number = parseInt(prompt('Число!', 0))
} while (isNaN(number));

let subtrahend = 0;
do{
    subtrahend = parseInt(prompt('Сколько отнять?', 0))
} while (isNaN(subtrahend));

let addNum = 0;
do{
    addNum = parseInt(prompt('Сколько прибавить?', 0))
} while (isNaN(addNum));

let multipler = 0;
do{
    multipler = parseInt(prompt('На cколько умножить?', 0))
} while (isNaN(subtrahend));

let divider = 1;
let isDividerValid = true;
do{
    divider = parseInt(prompt('На сколько разделить?', 1))
    isDividerValid = !isNaN(divider) && divider !== 0;

} while (!isDividerValid);

const result = (((number - subtrahend) + addNum) * multipler) / divider; //не 110

console.log(`((((${number} - ${subtrahend}) + ${addNum}) * ${multipler}) / ${divider} = ${result})`)


